* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #eee;
  background: #222;
  font-family: monospace;
  font-size: 16px;
  font-weight: 400;
}

:root {
  --gold-light: #d9a521;
  --gold-medium: #b38b1e;
  --gold-dark: #806113;
  --blue: #4b7bec;
  --teal: #2bcbba;
  --green: #78e08f;
  --yellow: #fed330;
  --orange: #fd9644;
  --red: #fc5c65;
}

.header {
  border-bottom: 1px solid #444;
  width: 100%;
  height: 70px;
  padding: 20px;
  font-size: 24px;
  font-weight: bold;
}

.main-container {
  justify-content: space-between;
  height: calc(100vh - 70px);
  display: flex;
}

.left-container {
  border-right: 1px solid #444;
  flex-direction: column;
  flex: 1;
  display: flex;
}

.right-container {
  width: 25%;
}

.parameters {
  border-bottom: 1px solid #444;
}

.box {
  padding: 20px;
}

.box-title {
  color: var(--gold-medium);
  text-transform: uppercase;
  border-bottom: 1px solid var(--gold-medium);
  font-weight: bold;
  display: inline-block;
}
